.form-label {
  @apply block mb-1 text-sm text-gray-700 select-none;
}

.form-input,
.form-textarea,
.form-select {
  @apply relative block w-full px-3 py-2 font-sans text-sm leading-normal text-left text-gray-700 bg-white border border-gray-300 rounded-md appearance-none focus:border-indigo-400 focus:ring;

  &::placeholder {
    @apply text-gray-500 opacity-100;
  }
}

.form-select {
  @apply pr-6;

  background-image: url('data:image/png;base64,');
  background-size: 0.7rem;
  background-repeat: no-repeat;
  background-position: right 0.7rem center;

  &::-ms-expand {
    @apply opacity-0;
  }
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  @apply border-red-500 focus:ring focus:ring-red-200;
}

.form-error {
  @apply text-red-700 mt-0.5 text-sm;
}
